.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.diagram{
  width: 500px;
  height: 500px;
  float:left;
}

.board{
  height: 50%;
  display:table;
}

.board-row {
  clear: both;
  content: "";
  display: table-row;
}

.board-cell{
	border: 1px solid #999999;
	display: table-cell;
  text-align: center;
  vertical-align: center;
	padding: 3px 10px;
}

.rotated{
  transform: rotate(-70deg);
  margin: 50px 0px 50px 0px;
  padding:0 !important;
}

.clickable{
  cursor: pointer;
}

.board-connection-enabled{
  width: 18px;
  height: 18px; 
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 18px 18px;
  display: inline-block;
}

.board-connection-disabled{
  width: 18px;
  height: 18px; 
  background-color: #AF4C50;
  border: none;
  color: white;
  padding: 18px 18px;
  display: inline-block;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
